html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

.contenedor {
  width: 95%;
  max-width: 1000px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Roboto", cursive;
}

h2 {
  font-size: 4rem;
  font-family: "Roboto", cursive;
}
h2::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-image: linear-gradient(to right, #3b4862 0%, #3b4862 50%, #6277a1 50%, #a2aec7 100%);
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.notavif.notwebp .header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../img/header_bg.jpg);
}

.webp .header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../img/header_webp);
}

.avif .header {
  background-image: linear-gradient(to right, rgba(59, 72, 98, 0.9) 0%, rgba(59, 72, 98, 0.9) 100%), url(../img/header_bg.avif);
}

.header {
  padding: 3rem 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
}
.header__contenido {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .header__contenido {
    max-width: 70rem;
  }
}
.header__heading {
  font-size: 3rem;
  color: #FFFFFF;
  font-weight: bold;
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
    font-size: 4rem;
  }
}
.header__descripcion {
  display: none;
  text-align: justify;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__descripcion {
    display: block;
  }
}
.header__imagen {
  display: none;
}
@media (min-width: 768px) {
  .header__imagen {
    display: block;
    position: absolute;
    right: 0;
    bottom: -10rem;
    max-width: 35rem;
    border-top-left-radius: 2rem;
    border-bottom-left-radius: 5rem;
  }
}
@media (min-width: 1200px) {
  .header__imagen {
    max-width: 45rem;
  }
}

.barra {
  display: flex;
  align-items: center;
  flex-direction: column;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}
.barra__logo img {
  max-width: 25rem;
  height: 10rem;
}

.nav__enlace {
  display: block;
  text-align: center;
  color: #FFFFFF;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .nav__enlace {
    display: inline-block;
    margin: 0 2rem 0 0;
  }
  .nav__enlace:last-of-type {
    margin-right: 0;
  }
}

.propiedades {
  padding: 5rem 0;
}
.propiedades__heading {
  font-weight: 400;
  margin: 2rem 0;
}
.propiedades__heading span {
  font-weight: bold;
}
.propiedades__grid {
  padding: 1rem;
  display: grid;
  grid-template-columns: repeat(6, 30rem);
  column-gap: 3.5rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
@media (min-width: 480px) {
  .propiedades__grid {
    padding: 3rem 3rem 3rem 0;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    overflow-x: hidden;
  }
}
@media (min-width: 768px) {
  .propiedades__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.propiedad {
  background-color: #FFFFFF;
  border-radius: 1rem;
  scroll-snap-align: center;
}
.propiedad__nombre {
  font-size: 2.3rem;
  margin: 0;
  color: #FFFFFF;
  position: absolute;
  bottom: 1rem;
  left: 1rem;
}
.propiedad__imagen {
  aspect-ratio: 1/0.7;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}
.propiedad__contenido {
  padding: 1rem;
}
.propiedad__descripcion {
  text-align: justify;
}
.propiedad__precio {
  color: #3b4862;
  font-size: 2.3rem;
  text-align: center;
  margin: 1rem 0;
}
.propiedad__precio span {
  color: #000;
  font-size: 2rem;
}
.propiedad__boton {
  background-color: #3b4862;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
}
@media (min-width: 768px) {
  .propiedad__boton {
    width: auto;
  }
}
.propiedad__boton:hover {
  background-color: #283142;
}
.propiedad:nth-child(1) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 75%, rgba(0, 0, 0, 0.8)), url("../img/propiedad_1.webp");
}
.propiedad:nth-child(2) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 75%, rgba(0, 0, 0, 0.8)), url("../img/propiedad_2.webp");
}
.propiedad:nth-child(3) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 75%, rgba(0, 0, 0, 0.8)), url("../img/propiedad_3.webp");
}
.propiedad:nth-child(4) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 75%, rgba(0, 0, 0, 0.8)), url("../img/propiedad_4.webp");
}
.propiedad:nth-child(5) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 75%, rgba(0, 0, 0, 0.8)), url("../img/propiedad_5.webp");
}
.propiedad:nth-child(6) .propiedad__imagen {
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0) 75%, rgba(0, 0, 0, 0.8)), url("../img/propiedad_6.webp");
}

.notavif.notwebp .destacada {
  background-image: linear-gradient(to bottom, rgba(59, 72, 98, 0) 0%, rgba(59, 72, 98, 0) 50%, #3b4862 50%, #3b4862 100%), url(../img/destacada.jpg);
}
@media (min-width: 768px) {
  .notavif.notwebp .destacada {
    background-image: linear-gradient(to right, rgba(59, 72, 98, 0) 0%, rgba(59, 72, 98, 0) 50%, #3b4862 50%, #3b4862 100%), url(../img/destacada.jpg);
  }
}

.webp .destacada {
  background-image: linear-gradient(to bottom, rgba(59, 72, 98, 0) 0%, rgba(59, 72, 98, 0) 50%, #3b4862 50%, #3b4862 100%), url(../img/destacada.webp);
}
@media (min-width: 768px) {
  .webp .destacada {
    background-image: linear-gradient(to right, rgba(59, 72, 98, 0) 0%, rgba(59, 72, 98, 0) 50%, #3b4862 50%, #3b4862 100%), url(../img/destacada.webp);
  }
}

.avif .destacada {
  background-image: linear-gradient(to bottom, rgba(59, 72, 98, 0) 0%, rgba(59, 72, 98, 0) 50%, #3b4862 50%, #3b4862 100%), url(../img/destacada.avif);
}
@media (min-width: 768px) {
  .avif .destacada {
    background-image: linear-gradient(to right, rgba(59, 72, 98, 0) 0%, rgba(59, 72, 98, 0) 50%, #3b4862 50%, #3b4862 100%), url(../img/destacada.avif);
  }
}

.destacada {
  padding: 3rem 0;
  margin-bottom: 5rem;
  background-size: 100%, cover;
  background-position: center, -45rem -20rem;
  background-repeat: no-repeat;
}
@media (min-width: 480px) {
  .destacada {
    background-position: center, -30rem -20rem;
  }
}
@media (min-width: 768px) {
  .destacada {
    background-position: center, -35rem -30rem;
  }
}
.destacada__grid {
  display: grid;
  grid-template-rows: repeat(2, 40rem);
}
@media (min-width: 480px) {
  .destacada__grid {
    grid-template-rows: repeat(2, 35rem);
  }
}
@media (min-width: 768px) {
  .destacada__grid {
    gap: 2rem;
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 1fr);
  }
}
.destacada__contenido {
  grid-row: 2/3;
  grid-column: 1/2;
  padding: 2rem;
}
@media (min-width: 768px) {
  .destacada__contenido {
    grid-row: 1/2;
    grid-column: 2/3;
  }
}
.destacada__heading {
  text-align: center;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .destacada__heading {
    text-align: left;
  }
}
.destacada__texto {
  color: #FFFFFF;
  text-align: justify;
}
.destacada__enlace {
  background-color: #FFFFFF;
  padding: 1.5rem 4rem;
  color: #3b4862;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
}
@media (min-width: 768px) {
  .destacada__enlace {
    width: auto;
  }
}
.destacada__enlace:hover {
  background-color: #e6e6e6;
}
.destacada__precio {
  font-size: 2.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: bold;
  margin-top: 0;
}
.destacada__precio span {
  font-weight: 400;
}

@media (min-width: 1024px) {
  .principal__grid {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 5rem;
  }
}

.blog__grid {
  margin: 3rem 0;
}
@media (min-width: 1024px) {
  .blog__heading span {
    display: block;
  }
}

.testimonial {
  margin: 3rem 0;
  background-color: #FFFFFF;
}
@media (min-width: 480px) {
  .testimonial {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
}
@media (min-width: 480px) {
  .testimonial__imagen {
    grid-column: 1/3;
  }
}
.testimonial__img {
  height: 30rem;
  object-fit: cover;
}
.testimonial__contenido {
  padding: 2rem 2rem 2rem 0;
}
@media (min-width: 480px) {
  .testimonial__contenido {
    grid-column: 3/6;
  }
}
.testimonial__heading {
  margin: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .testimonial__heading {
    text-align: left;
  }
}
.testimonial__meta {
  color: #000;
}
.testimonial__meta span {
  color: #3b4862;
}
.testimonial__descripcion {
  text-align: justify;
  margin: 0;
}
.testimonial__boton {
  background-color: #3b4862;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
}
@media (min-width: 768px) {
  .testimonial__boton {
    width: auto;
  }
}
.testimonial__boton:hover {
  background-color: #283142;
}
@media (min-width: 768px) {
  .testimonial__boton {
    display: inline-block;
  }
}

.aside {
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .aside {
    margin: 0;
  }
}
@media (min-width: 1024px) {
  .aside__heading span {
    display: block;
  }
}
.aside__imagen {
  width: 20rem;
  margin: 0 auto;
}
.aside__contenido {
  background-color: #FFFFFF;
  margin-top: -10rem;
  padding: 10rem 3rem 3rem 3rem;
  border-radius: 2rem;
}
.aside__nombre {
  text-align: center;
  font-size: 2rem;
  margin: 1rem 0;
}
.aside__texto {
  text-align: justify;
  position: relative;
}
.aside__texto::before {
  position: absolute;
  content: "";
  display: block;
  background-image: url(../img/quote.svg);
  background-repeat: no-repeat;
  width: 2rem;
  height: 1rem;
  top: 0;
  left: -1.5rem;
}